<template>
  <nav>
    <img src="../assets/logo2.png" alt="logo" class="nav-logo">
    <a href="#paginasweb">Paginas web</a>
    <a href="#sobremi">Sobre mi</a>
    <a href="#proyectos">Proyectos</a>
    <a href="#contacto">Contacto</a>
      <div class="mobile">
        <img src="../assets/icon-hamburger.svg" alt="" class="icon" @click="toggle()">
        <div id="mobile-links" :class="{hamburgerActive:hamburgerActive}">
          <div><a href="#paginasweb">Paginas web</a></div>
          <div><a href="#sobremi">Sobre mi</a></div>
          <div><a href="#proyectos">Proyectos</a></div>
          <div><a href="#contacto">Contacto</a></div>
        </div>
      </div>
  </nav>
</template>

<script setup>
import {ref} from "vue"
let hamburgerActive=ref(false)
let toggle=()=>hamburgerActive.value=!hamburgerActive.value
</script>

<style>
nav {
padding-left: 5%;
  padding-right: 10%;
  padding-top: 5px;
  padding: 5px 10%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-family: verdana;
  background-color: #333646;
}

nav a {
  margin-left: 40px;
  text-decoration: none;
  color: white;
  position: relative;
  font-size: 1rem;
}

nav a::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 1px;
  transform: scalex(0);
  transition: transform 300ms ease-in-out;
  background-color: white;
}

nav a:hover::before,
nav a:focus::before {
  transform: scalex(1);
}

nav .nav-logo {
  margin-right: auto;
  height: 40px;
  width: 40px;
}

 .mobile{
    display: none;
  }
  .mobile #mobile-links {
    /* display: none; */
    width: 100%;
    background-color: #252734;
    text-align: center;
    position: absolute;
    right: 0;
    top:45px;
    height: 0;
    overflow: hidden;
    transition:all .5s ease;
  }

  .mobile #mobile-links div {
    padding: 14px 16px;
  }

  .mobile a {
    color: white;
    font-size: 17px;
    display: inline;
    border-radius: 3em;
  }

  .mobile .icon {
    display: block;
    right: 0;
    top: 0;
    margin-right: auto;
  }

  /* .mobile a:hover {
    background-color: #ffd602;
  } */

  .mobile #mobile-links.hamburgerActive{
    display:block;
    height: 200px;

  }
  .icon{
    width:20px;
    height: 20px;
  }
  @media screen and ( max-width:760px) {
    .mobile{
      display:block;

    }
    nav > a{
      display: none;
    }
  }
</style>